@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
  font-family: Montserrat, sans-serif;
}
body {
  margin: 0;
  padding: 0;
}
.content {
  min-height: 100vh;
}

.nav ul {
  list-style-type: none;
  margin: 0;
  overflow: hidden;
  background-color: #3498db;

  padding: 1vh;

  padding-left: 5vw;
  padding-right: 5vw;
}

.nav li {
  float: left;
}

.nav li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;

  -webkit-transition-duration: 200ms;

          transition-duration: 200ms;
}

.nav-brand {
  font-weight: bold;
}

* {
  font-family: Montserrat;
  font-style: normal;
}

.title {
  text-align: center;
  color: #3498db;
  padding-top: 40vh;
  padding-bottom: 40vh;
}

.title h2 {
  margin: 0;
  vertical-align: middle;
}

.title h1 {
  margin: 0;
  font-size: 80pt;
}

.features {
  padding: 8vw;
  padding-top: 1em;
  padding-bottom: 3em;
  background-color: #3498db;
  color: #ffffff;
}

.section-header {
  font-weight: bold;
  font-size: 50px;
  color: white;
}

.feature {
  padding-top: 20px;
  padding-bottom: 22px;
}

.feature h3 {
  font-weight: bold;
  font-size: 30px;
  line-height: 10px;
  display: inline;
}

.feature i {
  font-weight: 900;
  font-size: 100px;
  display: inline-block;
  float: right;
}

.feature span {
  max-width: 80%;
  word-wrap: break-word;
  display: block;
  margin: 0;
  font-size: 18pt;
}

.trusted-by {
  padding: 8vw;
  padding-top: 1em;
  padding-bottom: 3em;
  background-color: #2980b9;
  color: #ffffff;
}

/* er ist neun und er wrapt */
.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
}

.trusted-by .wrapper-item {
  background-color: #3498db;
}

.team .wrapper-item {
  background-color: #2980b9;
  min-height: 10vh;
}

.wrapper-item {
  border-radius: 7px;
  display: inline-block;
  margin: 1em;
  padding: 1vh;
  font-size: 18px;
  color: white;
}

.wrapper-item h3 {
  margin: 0;
}

.team {
  background-color: #3498db;
  padding: 8vw;
  padding-top: 1em;
  padding-bottom: 3em;
}

.alert * {
  margin: 0;
  vertical-align: middle;
  display: flex;
  justify-content: center;
}
.alert {
  padding: 3vh;
  color: #fff;
  background: #e74c3c;
  width: 100vw;
  height: 4vh;
}

.main {
  width: 390px;
  height: 550px;
  background: #fff;
  margin: 100px auto;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.logo {
  top: 50px;
  width: 100px;
  height: 100px;
  margin: auto;
  background: #000;
  position: relative;
  border-radius: 100px;
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  background: #3498db;
  background: -webkit-linear-gradient(to right, #3498db, #3498db);
  background: -webkit-gradient(linear, left top, right top, from(#3498db), to(#3498db));
  background: -webkit-linear-gradient(left, #3498db, #3498db);
  background: linear-gradient(to right, #3498db, #3498db);
}

.logo h3 {
  color: #fff;
  font-weight: 200;
  line-height: 100px;
}

.main form {
  top: 100px;
  position: relative;
}

.main form input {
  display: block;
  margin: 10px auto;
}

.main form h4 {
  font-size: 12px;
  margin-top: 2em;
}

.main h4 a {
  color: #3498db;
  text-decoration: none;
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
}

.main h4 a:hover {
  color: #3498db;
}

.main input[type="username"],
.main input[type="password"],
.main input[type="text"] {
  width: 60%;
  outline: none;
  border-top: 0;
  color: #3498db;
  border-left: 0;
  border-right: 0;
  font-size: 14px;
  padding: 26px;
  font-weight: 300;
  border-bottom: 1px solid #3498db;
}

.main input[type="submit"] {
  color: #fff;
  width: 150px;
  height: 50px;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 16px;
  margin-top: 50px;
  border-radius: 4px;
  -webkit-transition: 200ms ease-out;
  transition: 200ms ease-out;
  box-shadow: 0 0 10px #3498db;
  background: #3498db;
  background: -webkit-linear-gradient(to right, #3498db, #3498db);
  background: -webkit-gradient(linear, left top, right top, from(#3498db), to(#3498db));
  background: -webkit-linear-gradient(left, #3498db, #3498db);
  background: linear-gradient(to right, #3498db, #3498db);
}

.main input[type="submit"]:hover {
  width: 9.6875em;
  height: 3.25em;
  box-shadow: 0 0 0.5em #3498db;
}

.fa-user {
  color: #3498db;
  position: absolute;
  top: 1em;
  left: 2.2em;
  font-size: 18pt !important;
  cursor: text;
  -webkit-transition: 0.2s ease-out;
  transition: 0.2s ease-out;
}

.main .fa-lock {
  color: #3498db;
  position: absolute;
  top: 4.2em;
  left: 2.2em;
  font-size: 18pt !important;
  cursor: text;
  -webkit-transition: 0.2s ease-out;
  transition: 0.2s ease-out;
}

.main [placeholder]::-webkit-input-placeholder {
  color: #3498db;
  padding-left: 0.5em;
}

.main [placeholder]:focus::-webkit-input-placeholder {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  -webkit-transform: translate(10px, 0);
  transform: translate(10px, 0);
  opacity: 0;
}

.main :required {
  font-family: "Montserrat";
}

.pwDiv {
  width: 390px;
  height: 350px;
  background: #fff;
  margin: 100px auto;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.pwDiv h3 {
  margin: 0;
  padding: 1vw;
}

.pwDiv button {
  color: #fff;
  width: 150px;
  height: 50px;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 16px;
  margin-top: 1vw;
  border-radius: 4px;
  -webkit-transition: 200ms ease-out;
  transition: 200ms ease-out;
  box-shadow: 0 0 10px #3498db;
  background: #3498db;
  background: -webkit-linear-gradient(to right, #3498db, #3498db);
  background: -webkit-gradient(linear, left top, right top, from(#3498db), to(#3498db));
  background: -webkit-linear-gradient(left, #3498db, #3498db);
  background: linear-gradient(to right, #3498db, #3498db);
}

.dashboardHeadline {
  font-size: 40px;
  color: #3498db;
  margin-top: 1vh;
  margin-bottom: 1vh;
  margin-left: 5%;
}

.greeting {
  margin: 0;
  margin-bottom: 1vh;
  color: #fff;
}

.row {
  display: grid;
  grid-template-columns: 50% 50%;
}

.dashboard {
  margin: 5vw;
  margin-top: 1vh;
  margin-bottom: 3vh;

  background-color: #3498db;
  border-radius: 5px;
  padding: 1.5vh;
}

.container-headline {
  color: black;
  margin: 0;
  display: inline;
}

.dashboard h6 {
  color: #ffffff;
  font-size: 25px;
  margin: 0;
}

.container {
  margin-top: 1vh;
  margin-right: 0.5vh;
  margin-left: 0.5vh;
  padding: 1vh;
  background-color: #fff;
  border-radius: 0.3em;
}

.wrapper {
  display: grid;
  border-radius: 0.3em;
}

.wrapper-today {
  background-color: #e74c3c;
  border-radius: 0.3em;
  margin: 2.5%;
}

.wrapper-tomorrow {
  background-color: #f1c40f;
  border-radius: 0.3em;
  margin: 2.5%;
}

.wrapper-future {
  background-color: #2ecc71;
  border-radius: 0.3em;
  margin: 2.5%;
}

.wrapper h3 {
  margin-left: 4.5%;
}

.subject {
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
}

.text {
  list-style-type: none;
  font-weight: 100;
  font-size: 15px;
}

.white {
  color: white;
}
.TeamText {
  font-family: "Montserrat";
  font-size: 17px;
}
html {
  max-width: 100%;
  overflow-x: hidden;
}

.assignmentForm input,
select {
  outline: none;
  color: #3498db;
  font-size: 14px;
  font-weight: bold;
