  font-size: 18px;
  color: #2ecc71;
  padding: 0.5em 1em;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  cursor: pointer;
}

.assignmentForm input[type="submit"]:hover {
  background: #2ecc71;
  color: #fff;
  border-color: #2ecc71;
}

.assignmentForm ::-webkit-input-placeholder {
