}
.assignmentForm ::-moz-placeholder {
  color: #3498db;
  font-size: 16px;
}
.assignmentForm :-ms-input-placeholder {
  color: #3498db;
  font-size: 16px;
}
.assignmentForm ::-ms-input-placeholder {
  color: #3498db;
  font-size: 16px;
}
.assignmentForm ::placeholder {
