  font-weight: bold;
  border: 1px solid #3498db;
  border-radius: 5px;
  block-size: 100%;
  width: 100%;
  height: 5vh;
  margin: 0.1vh;
  box-sizing: border-box;
}

.teamForm ::-webkit-input-placeholder {
  color: #3498db;
  font-size: 16px;
}
.teamForm ::-moz-placeholder {
  opacity: 1;
  color: #3498db;
  font-size: 16px;
